//SVG image and login are placed over here. Ther is no logic taking place in this particular file
//logic will be handled in the Textfield component

function RegistrationPage(){

    return(
        <>
            {/* spltscreen splits the screen into 2 seperate areas */}
            <SplitscreenLayout>
                
                <div className="ml-5">
                
                    <SVGComponent svg={registration_image}/>
                </div>
    
                <RegistrationComponent />
            </SplitscreenLayout>
        </>
    )
}

export default RegistrationPage;